import * as THREE from 'three'
import { useEffect, useRef, useState , useMemo } from 'react'
import { Canvas, extend, useLoader, useFrame } from '@react-three/fiber'
import { useGLTF,OrbitControls,PerspectiveCamera, useCursor, MeshReflectorMaterial, Image, Text, Environment,useAnimations, } from '@react-three/drei'
import { gsap } from "gsap";
import useSound from 'use-sound';
import sample from './bg-video.mp4';
import ambientAudio from './ambient-audio.mp3';

const GOLDENRATIO = 1.61803398875
let meshVisible = true


function Controls() {
  const group = useRef()
  useFrame((state, dt) => {
    if(group.current.getDistance() < 2) {
      meshVisible = false
    }else {
      meshVisible = true
    }
  })
  return (
    <OrbitControls
    ref={group}
    makeDefault
    autoRotate
    autoRotateSpeed={0.3}
    maxPolarAngle={Math.PI / 2.3}
    minPolarAngle={Math.PI / 2.3}
    enableZoom={true}
    enablePan={false}
    minDistance={1}
    maxDistance={1.8}
  />
  )
}

function Model({ ...props }) {
  const group = useRef()
  const { nodes, materials } = useGLTF('/card2updated_10.glb')
  let isDragging = false
  let deltaYCounter = 0
  const mouse = {
    x: undefined,
    y: undefined
  }

  const pointerDown = () => {
    isDragging = true
  }

  const pointerUp = () => {
    isDragging = false
  }

  const pointerMove = (e) => {
    mouse.x = (e.clientX/window.innerWidth) * 2 - 1
    
    if(group.current) {
      if(isDragging) {
        const toRotate = group.current.rotation.y + (mouse.x * 0.35)
        
        gsap.to(group.current.rotation, { 
          y: toRotate,
          duration : 1.2
        });
      }
    }

  }

  const handleZoom = (e) => {
    let direction = 1
    let animateZoom = true

    if(group.current) {
      if(e.deltaY < 0) {
        deltaYCounter ++
      }else if(e.deltaY > 0) {
        deltaYCounter --
        direction = -1
      }
      console.log(group);
      const toScale = group.current.scale.y + (direction * Math.abs(deltaYCounter) * 0.0004)
      
      if(toScale > 0.42) {
        animateZoom = false
        deltaYCounter --
      }
        
      if(toScale < 0.3) {
        deltaYCounter ++
        animateZoom = false
      }
      
      if(animateZoom) {
        gsap.to(group.current.scale, { 
          x: toScale,
          y: toScale,
          z: toScale,
          duration : 2
        });
      }
    }
    

  }

  // const rotate = useFrame(() => {
  //   // group.current.rotation.x += 0.01;
  //   // group.current.rotation.y += 0.01;
  //   // group.current.rotation.z += 0.01;
  // });
  window.addEventListener("pointerdown", pointerDown);
  window.addEventListener("pointerup", pointerUp);
  window.addEventListener("pointermove", pointerMove);
  
  window.addEventListener("wheel", handleZoom);
  
	const front = useLoader(THREE.TextureLoader, '/front-img-og.jpg')
	const back = useLoader(THREE.TextureLoader, '/back-img-sm.jpg')

  return (
    <group ref={group} {...props} dispose={null}>
      <group scale={0.01}>
        <group position={[-16.7, -45.25, 10.99]} rotation={[Math.PI, -0.06, Math.PI]}>
          <group position={[-34.25, 139.87, 7.36]} rotation={[0, -0.07, 0]}>
            <group position={[4.27, -280.44, 118.34]}>
              <mesh geometry={nodes['Cylinder1-Cylinder-Physical001'].geometry} material={materials['Material.010']} />
              <mesh geometry={nodes['Cylinder1-Cylinder007'].geometry} material={materials['Material.010']} />
            </group>
            <group position={[1.04, -0.02, -60.79]}>
              <mesh geometry={nodes['Cylinder1-Cylinder009'].geometry} material={materials['Material.010']} />
              <mesh geometry={nodes['Cylinder1-Cylinder1-Physical001'].geometry} material={materials['PBR.004']} />
            </group>
            <group position={[4.27, 0, 172.73]}>
              <mesh geometry={nodes['Cylinder1-Cylinder011'].geometry} material={materials['Material.010']} />
              <mesh geometry={nodes['Cylinder1-Cylinder2-Physical001'].geometry} material={materials['PBR.004']} />
            </group>
            <group position={[1.04, -280.44, -113.33]}>
              <mesh geometry={nodes['Cylinder1-Cylinder013'].geometry} material={materials['Material.010']} />
              <mesh geometry={nodes['Cylinder1-Cylinder3-Physical001'].geometry} material={materials['PBR.004']} />
            </group>
            <mesh geometry={nodes.Cylinder004.geometry} material={materials['Material.001']} position={[14.29, 92.97, -115.02]} rotation={[0, 0, -Math.PI / 2]} />
            <mesh geometry={nodes.Cylinder005.geometry} material={materials['PBR.004']} position={[17.47, -281.33, 116.83]} rotation={[0, 0, -Math.PI / 2]} />
            <mesh geometry={nodes.Cylinder006.geometry} material={materials['Material.007']} position={[14.29, -281.38, -115.02]} rotation={[0, 0, -Math.PI / 2]} />
            <mesh geometry={nodes.Cylinder007.geometry} material={materials['Material.009']} position={[17.47, 92.97, 118.26]} rotation={[0, 0, -Math.PI / 2]} />
          </group>
        </group>
        <group position={[6.74, -45.25, 9.59]} rotation={[0, 0.04, 0]}>
          <group position={[-1.19, 45.75, -9.65]} rotation={[-Math.PI, 0.04, -Math.PI]}>
            <mesh geometry={nodes['Card_Insert_here-Front-PBR003'].geometry} material={materials['PBR.1']} material-emissiveIntensity={1.2} material-emissiveMap={front}/>
            <mesh geometry={nodes['Card_Insert_here-Front-PBR004'].geometry} material={materials['PBR.2']} material-emissiveIntensity={1.38} material-emissiveMap={back}/>
          </group>
          <mesh geometry={nodes.MetaVerse_Logo001.geometry} material={materials['PBR.007']} position={[-11.04, -136.91, -11.97]} rotation={[Math.PI / 2, 0, 1.61]} />
        </group>
        <group position={[1.23, 0.81, 23.36]} rotation={[0, -1.57, 0]}>
          <group position={[-13.03, 0, -4.92]} rotation={[0, 1.57, 0]}>
            <mesh geometry={nodes.Black_Plate002_1.geometry} material={materials['Material.011']} />
            <mesh geometry={nodes.Black_Plate002_2.geometry} material={materials['Material.012']} />
          </group>
        </group>
        <mesh geometry={nodes.Cube002.geometry} material={materials['PBR.004']} position={[-8.82, 0.81, -0.01]} rotation={[Math.PI, 0, Math.PI]} material-opacity={0.065}/>
        <mesh geometry={nodes.Cube003.geometry} material={materials['PBR.004']} position={[11.22, 0.81, -0.23]} rotation={[Math.PI, 0, Math.PI]} material-opacity={0.065}/>
      </group>
    </group>
  )
}



export default function App({ }) {
  let volume = 0.03
  const [soundPlayed, setSoundPlayed] = useState(false);
  const [play, { stop }] = useSound(ambientAudio, {
    volume: volume,
    interrupt: true,
    onend: () => {
      stop()
    },  
  });

  const handleVideoEnded = () => {
    document.querySelector('.videoTag').classList.add('hide')
    const volumeInterval = setInterval(() => {
      if(volume < 0) {
        stop()
        clearInterval(volumeInterval)
      }
      volume -= 0.0005
    }, 10);
  }
  
  useEffect(() => {
    setTimeout(() => {
      play()
      setSoundPlayed(true)
    }, 1000);
  });

  return (
    <div className='backgroundDiv'>
      <video className='videoTag' autoPlay muted onEnded={handleVideoEnded}>
          <source src={sample} type='video/mp4' />
      </video>
    {/* <BackgroundDiv /> */}
    <Canvas gl={{ alpha: true }} dpr={[1, 1.5]} >
      <Environment // Whether to affect scene.background
              files={'reflection-map_BW04.hdr'}
              path={'/'}
      />
      <Model scale={[0.3,0.3,0.3]} position={[0, 0.0, 0]} rotation={[0, Math.PI/2, 0]}/>
      <PerspectiveCamera makeDefault fov={55} position={[0, 0, 2]}>
      </PerspectiveCamera>
      {/* <Controls /> */}
      <rectAreaLight
        position={[0, -0.1, 1]}
        color="#091f68" 
        visible={true}
        intensity={5}
/>
    </Canvas>
    </div>
  )
}
